/*
 * Typography
 *
 * Theme font families
 * Font family sass function
 */

 $font-path: "/wp-content/themes/a16z-2015/client/fonts";
 $image-path: "/wp-content/themes/a16z-2015/client/images";

// Font Family Stacks
$font-families: (
	sans-serif: "'proxima-nova', Helvetica, Arial, sans-serif",
	serif: "Garamond, Baskerville, 'Baskerville Old Face', 'Hoefler Text', 'Times New Roman',  serif",
	logotype: "serif",
);

$lust: 'lust', serif;
$din-condensed: 'din-condensed', sans-serif;

// SCSS Fonts Map Shortcut Function
@function font-family($key) {
	@if map-has-key($font-families, $key) {
		@return unquote(map-get($font-families, $key));
	}

	@warn 'Unknown `#{$key}` in $font-families.';

	@return null;
}

// Default Font Declarations
$font-body: 1rem/1.667 font-family(sans-serif);
$font-header: 700 1.375rem/1.625rem font-family(sans-serif);
$font-title: 1rem/1 font-family(logotype);

/* Header Mixins */
@mixin header() {
	font: $font-header;
	letter-spacing: calc(-5 / 1000em);
	color: $steel;
}

@mixin section-header() {
	border-bottom: $border-light;
	display: flex;
	margin-bottom: rem(24);
	padding-bottom: rem(20);
}

@mixin section-title() {
	@include header();
	font-size: rem(36);
	line-height: 1;
}

@mixin section-subheader() {
  @include header();
  font-weight: 500;
  font-size: rem(28);
  line-height: 1;
  color: #9B9B9B;
}

@mixin page-title() {
	@include header();
	font-size: rem(40);
	line-height: 1;
}

@mixin section-subtitle() {
	font-weight: 500;
	text-transform: uppercase;
}

@mixin post-header() {
	@include header();
	font-weight: 500;
}

@mixin action-link() {
	@include link-hover($color-secondary);

	@include breakpoints(medium) {
		font-weight: 500;
	}
}

@mixin screen-reader-text() {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal;
}
