/* SCSS Utilities
 *
 * Helper mixins and functions for use throughout the theme
 */

// Space using rem grid.
@function spacer($units, $unit: rem) {
	@if ($unit == 'em') {
		@return ($units * 1em);
	}

	@return ( $units * 1rem );
}

/**
 * Style resets & mixins
 */

// Reset default browser button styles
@mixin button-reset($color: none) {
	background: $color;
	border: none;
	font-size: inherit;
	padding: 0;
}

// Clearfix
@mixin clearfix {

	&::after {
		clear: both;
		content: '';
		display: table;
	}
}

// Variable Width Full Bleed Mixin
@mixin full-bleed($width: 100vw) {
	margin-left: 50%;
	transform: translateX(-50%);
	width: #{$width};
}

// Pins an element to fill a relative container
@mixin absolute-pinned {
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
}

// Hide nested list items
@mixin hide-submenu {

	li ul {
		display: none;
	}
}

// Link hover effect
@mixin link-hover( $link-color: $steel, $hover-color: $poppy ) {
	text-decoration: none;
	color: $link-color;

	&:hover {
		color: $hover-color;
	}
}

// Horizontal Separator
@mixin separator($character: '|', $spacing: spacer(1)) {

	&::after {
		content: $character;
		margin: 0 $spacing;
	}

	&:last-child {

		&::after {
			display: none;
		}
	}
}

// Hide element text
@mixin hide-text {
	overflow: hidden;
	text-indent: 100vw;
	white-space: nowrap;
}

// Hide element text to replace with a background image
@mixin text-replace($height: 1em, $width: 1em) {
	@include hide-text();
	background-position: center;
	background-repeat: no-repeat;
	display: block;
	height: $height;
	width: $width;
}

// Duplicates necissary styles for a svg mask fallback
@mixin svg-mask($color, $image) {
	background-image: url($image);
	background-position: center;
	background-repeat: no-repeat;
	background-size: 100% auto;
	color: $color;

	@supports (mask-image: url()) {
		background-color: $color;
		background-image: none;
		mask-image: url($image);
		mask-position: center;
		mask-repeat: no-repeat;
		mask-size: 100% auto;
	}
}

// Adds vertical spacing to all child elements but the last
@mixin vertical-rhythym($margin: spacer(1), $padding: 0, $border: none) {

	> * {
		border-bottom: $border;
		margin-bottom: $margin;
		padding-bottom: $padding;

		&:last-child {
			border-bottom: none;
			margin-bottom: 0;
			padding-bottom: 0;
		}
	}
}

/**
 * SCSS Dev Utilities
 */

// Highlight an element
@mixin dev-block($color: red, $background: rgba(255,255,0,0.1), $outline: 1px dotted $color) {
	outline: $outline;
	background: $background;
}

// objectfit
%objectfit {
  position: relative;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

// listreset
%listreset {
	list-style: none;
	padding: 0;
	margin: 0;
}
