/* Core CSS Colors
 *
 * List of named colors for use in CSS.
 * SCSS maps `color()` helper function
 */

// Named Project Font Colors
$colors: (
	black: #0B0B0B,
	gray-very-dark: hsl(0,0,20),
	gray-dark: hsl(0,0,40),
	gray: hsl(0,0,50),
	gray-light: hsl(0,0,66),
	gray-very-light: hsl(0,0,82),
	gray-slate: #40474e,
	white: #FFFFFF,
  orange: #ED8B00,
  gray-blue: #7a99ac,
  gray-slate-blue: #4b4f54,

);

// SCSS Color Map Shortcut Function

@function color($key) {
  @if map-has-key($colors, $key) {
    @return map-get($colors, $key);
  }
  @warn "Unknown `#{$key}` in $colors.";

  @return null;
}

// Default Project Colors
$color-primary: color(gray-slate);
$color-secondary: color(gray-very-light);
$color-tertiary: color(gray-very-dark);
$color-highlight: color(orange);

// Books Landing Colors
$color-beige: #efe9dc;
$color-orange: #fdb853;
$color-darktan: #a9903e;
$color-darkdarktan: #7e6821;
$color-navy: #042a51;
$color-grayblue: color(gray-blue);

// Vertical Landing Pages
$blue-bg: color(gray-blue);

