/* Core Layout Values
 *
 * Default theme values
 * Theme layout mixins
 */

/* SCSS Layout Variables */

// Common Layout Values
$base-size: 16; // Base size in px
$content-max-width: 40rem;
$logo-image-ratio: calc(13 / 209 * 100%);
$site-max-width: rem(1180);
$footer-line-height: rem(30);
$three-column-spacer: rem(24);

// Common Visual Styles
$border-light: 1px solid color(gray-very-light);
$border-medium: 1px solid color(gray-light);
$border-dark: 1px solid $steel;

/**
 * SCSS Theme Layout Utilities
 */

// Makes an image frame at a width & height ratio
@mixin intrinsic-image($width, $height, $vertical-align: middle) {
  height: 0;
  overflow: hidden;
  padding-top: calc((#{$height}/#{$width}) * 100%);
  position: relative;

  > img,
  > source {
    position: absolute;

    @if ($vertical-align == 'top') {
      top: 0;
    } @else if ($vertical-align == 'bottom') {
      bottom: 0;
    } @else {
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
